body {
    font-family: 'HelveticaNeueCyr';
    font-weight: 300;
    font-variant: normal; }

.page {
  width: 100%; }

.wrapper {
  width: 100%;
  height: auto;
  margin: 0 auto;
  background-color: #ccc;

  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: flex-start; }

.frame {
  width: 86.74963396778917%;
  margin: 0 auto;
  padding: 0 15px;
  display: flex; }


// media
@media screen and (min-width:1367px ) {
  .frame {
    width: 1280px; } }



.index_form_in::-webkit-input-placeholder {
    text-indent: 0px;
    transition: text-indent 0.3s ease;
